.bg-dark {
  background-color: #000000 !important;
}

.artenix-logo {
  width: 90%;
}

.artenix-navbar {
  margin-right: 10%;
}

.banner {
  background-color: #000;
}

.banner-image {
  width: 100%;
  height: 50rem !important;
}

.banner-text {
  color: #fff;
  .art {
    color: #e48632;
  }
}

.page {
  background-image: url("./background.png");
}

.service-title {
  color: #fff;
}

.services-box {
  padding: 2%;
  background-color: #000;
  color: #fff;
  h5 {
    color: #e48632;
  }
}

.contact-box {
  padding: 5%;
  background-color: #000;
  color: #fff;
  h5 {
    color: #e48632;
  }

  .form-control {
    background-color: transparent;
    border: transparent;
    border-bottom: 1px solid #e48632;
  }
}

.submit-button {
  background-color: #e48632;
}

hr {
  display: block;
  border-top: 6px solid #e48632;
  margin: 1em 0;
  padding: 0;
}

.about-box {
  h4 {
    color: #fff;
    text-align: center;
  }
  padding: 2%;
  margin: 1%;
  background-color: #000;
  color: #fff;
}

.about-icon {
  width: 100px;
}
